html {
  font-size: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Space Grotesk", sans-serif;
  color: hsl(0deg, 0%, 85%);
  background-color: hsl(0deg, 0%, 8%);
  position: relative;
  overflow-x: hidden;
}

ol,
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  width: min(69.375rem, 100% - 3.75rem);
  position: relative;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1;
  font-weight: 700;
  color: hsl(0deg, 0%, 100%);
}

h1, h2 {
  font-size: 2.5rem;
  letter-spacing: -0.15625rem;
}
@media (min-width: 40em) {
  h1, h2 {
    font-size: 4.5rem;
  }
}
@media (min-width: 64em) {
  h1, h2 {
    font-size: 5.5rem;
  }
}

h3 {
  font-size: 1.5rem;
}

p {
  margin: 0;
  color: hsl(0deg, 0%, 85%);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.button {
  border: none;
  cursor: pointer;
  font-weight: 700;
  font-size: 1rem;
  max-width: fit-content;
  letter-spacing: 0.125rem;
  padding-bottom: 0.625rem;
  color: hsl(0deg, 0%, 100%);
  text-transform: uppercase;
  background-color: transparent;
  border-bottom: 0.125rem solid hsl(153deg, 71%, 59%);
  transition: color 150ms linear;
}
.button:is(:hover, :focus-visible) {
  color: hsl(153deg, 71%, 59%);
}

.navigation {
  display: grid;
  place-items: center;
  gap: 1.25rem;
  padding-top: 1.25rem;
}
@media (min-width: 40em) {
  .navigation {
    grid-auto-flow: column;
    justify-content: space-between;
    padding-top: 2.5rem;
  }
}
.navigation__title {
  color: hsl(0deg, 0%, 100%);
  font-size: clamp(1.5rem, 1rem + 1.5vw, 2rem);
}
.navigation__social-list {
  display: flex;
  gap: 1.25rem;
}
@media (min-width: 40em) {
  .navigation__social-list {
    margin-right: 1.875rem;
  }
}
.navigation .icon {
  height: 1.5rem;
  width: 1.5rem;
  fill: hsl(0deg, 0%, 100%);
}
.navigation .icon:is(:hover, :focus-visible) {
  fill: hsl(153deg, 71%, 59%);
}

.hero {
  display: grid;
  place-items: center;
  text-align: center;
  gap: 1.25rem;
  padding-bottom: 2.5rem;
}
.hero__content {
  display: grid;
  gap: 1.875rem;
  justify-items: center;
}
@media (min-width: 40em) {
  .hero {
    grid-template-areas: "content photo";
    text-align: left;
  }
  .hero__content {
    grid-area: content;
    justify-items: start;
    gap: 2.5rem;
  }
  .hero__title {
    width: max(120%, 10ch);
  }
  .hero__subtitle {
    max-width: 35ch;
  }
}
@media (min-width: 40em) and (min-width: 40em) {
  .hero__subtitle {
    font-size: 1.125rem;
  }
}
.hero__image {
  width: 10.9375rem;
  position: relative;
  grid-area: photo;
  margin-top: -5.8125rem;
  z-index: -2;
}
@media (min-width: 40em) {
  .hero__image {
    width: 20.125rem;
  }
}
@media (min-width: 64em) {
  .hero__image {
    width: 28.125rem;
  }
}
.hero__title span {
  display: inline-block;
  border-bottom: 0.375rem solid hsl(153deg, 71%, 59%);
}
.hero::before {
  content: url("../../../images/pattern-rings.be49833a.svg");
  position: absolute;
  top: 10%;
  left: -16.5625rem;
  z-index: -3;
}
.hero::after {
  content: url("../../../images/pattern-circle.ad1ffe0a.svg");
  position: absolute;
  bottom: 50%;
  right: -6.25rem;
  z-index: -1;
}
@media (min-width: 40em) {
  .hero::after {
    bottom: 0;
  }
}
@media (min-width: 64em) {
  .hero::after {
    bottom: 10%;
    right: 35%;
  }
}

.skills {
  padding: 2.5rem 0;
  margin-bottom: 3.125rem;
  border-block: 1px solid hsl(0deg, 0%, 100%);
}
@media (min-width: 40em) {
  .skills {
    border-bottom: none;
  }
}
.skills__list {
  display: grid;
  justify-items: center;
  text-align: center;
  gap: 1.5rem;
}
@media (min-width: 40em) {
  .skills__list {
    grid-template-columns: repeat(2, 1fr);
    justify-items: start;
    text-align: left;
    row-gap: 2.5rem;
  }
}
@media (min-width: 64em) {
  .skills__list {
    grid-template-columns: repeat(3, 1fr);
  }
}
.skills__name {
  color: hsl(0deg, 0%, 100%);
  font-size: 2rem;
  padding-bottom: 0.625rem;
}
@media (min-width: 40em) {
  .skills__name {
    font-size: 3rem;
  }
}
@media (min-width: 40em) {
  .skills__experience {
    font-size: 1.125rem;
  }
}
.skills::before {
  content: url("../../../images/pattern-rings.be49833a.svg");
  position: absolute;
  right: -23.75rem;
  bottom: 0;
}

.projects {
  padding-bottom: 6.25rem;
}
.projects__heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2.5rem;
}
.projects__list {
  display: grid;
  gap: 2.5rem;
}
@media (min-width: 40em) {
  .projects__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
.projects__card {
  display: grid;
  gap: 1.25rem;
}
.projects__picture {
  position: relative;
}
@media (min-width: 64em) {
  .projects__picture:is(:hover, :focus-visible) .projects__thumbnail {
    opacity: 0.2;
    cursor: pointer;
  }
  .projects__picture:is(:hover, :focus-visible) .projects__buttons--thumbnail {
    display: flex;
  }
}
.projects__thumbnail {
  width: 100%;
  transition: opacity 100ms linear;
}
.projects__title {
  text-transform: uppercase;
}
.projects__skills-list {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  gap: 0.625rem;
}
.projects__skills-list li {
  font-size: 1.125rem;
}
.projects__buttons {
  display: flex;
  align-items: center;
  gap: 1.25rem;
}
@media (min-width: 64em) {
  .projects__buttons {
    display: none;
  }
}
.projects__buttons--thumbnail {
  display: none;
  position: absolute;
  justify-content: center;
  align-items: center;
  gap: 1.25rem;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
}

.footer {
  background-color: hsl(0deg, 0%, 14%);
  padding-bottom: 1.875rem;
}
.footer__contact {
  display: grid;
  padding-block: 3.125rem;
  border-bottom: 1px solid hsl(0deg, 0%, 100%);
  gap: 2.5rem;
}
@media (min-width: 40em) {
  .footer__contact {
    grid-template-columns: repeat(2, 1fr);
  }
}
.footer__content {
  display: grid;
  gap: 2.5rem;
  height: fit-content;
  text-align: center;
}
@media (min-width: 40em) {
  .footer__content {
    text-align: left;
    max-width: 42ch;
  }
}
.footer__subtitle {
  font-size: 1.125rem;
}
.footer__form {
  display: grid;
  gap: 2.5rem;
}
.footer__form input, .footer__form textarea {
  border: none;
  outline: none;
  font-weight: 700;
  font-size: 1rem;
  font-family: inherit;
  text-transform: uppercase;
  color: hsl(0deg, 0%, 85%);
  background-color: hsl(0deg, 0%, 14%);
  padding: 0 0 1.5rem 1.0625rem;
  border-bottom: 1px solid hsl(0deg, 0%, 100%);
  border-radius: 0;
  resize: none;
  width: 100%;
  height: 2.875rem;
}
.footer__form input:focus, .footer__form textarea:focus {
  border-color: hsl(153deg, 71%, 59%);
}
.footer__form input:focus:invalid, .footer__form textarea:focus:invalid {
  background-image: url("../../../images/icon-error.svg");
  background-repeat: no-repeat;
  background-position: 100% 0;
  border-color: hsl(7deg, 100%, 68%);
}
.footer__form input:focus:invalid ~ .error, .footer__form textarea:focus:invalid ~ .error {
  display: block;
}
.footer__form .error {
  display: none;
  color: hsl(7deg, 100%, 68%);
  text-align: right;
  padding-top: 0.3125rem;
}
.footer__form button {
  justify-self: end;
}